export const novel = `　僕の心を掴んで離さなかった"彼女"は、
　異世界のお姫様でも、ひとりぼっちの少女でも、
　生まれながらに差をつけられるショウ者でもなく、幼馴染みの"青年"だった――

　朝の日差しがわずかに残る気持ちの良い時間帯、僕は片付けが一段落すると姉が淹れてくれたお茶で一息つく。
　「そっちはどう？ヴァレン。」
　姉さんが尋ねてくるのに僕は頷いて「だいぶ片付いたから、もう少し」と言葉にしようとしたが、扉の開閉音に視線をとられて音にはならなかった。
　「よう、ヴァレン、タナ。」
　「ナセル！」
　後ろより少しだけ長い横髪を揺らして彼は挨拶をする。ナセルは僕の幼馴染みだ。
　彼と出会ったのは確か―１２年ほど前だったろうか。家々の隙間をぬって入り込める小道の先の小さな空き地の木の下で、彼は気持ち良さそうに眠っていた。出会ってからはすぐに仲良くなって、暇さえあれば連れだって遊びに行くのだ。
　とはいえ。
　姉さんがナセルへ腰かけるよう誘うと、反対に僕は椅子から立ち上がる。どうかしたのかと彼が口を曲げる。
　「僕は片付けを終わらせるよ。じゃ、ごゆっくり。」
　にこりと笑顔を作る。お前な、と苦笑いを返された。茶碗だけ流しへ運んで、自室への階段を上る。だって邪魔者は退散するのが一番いいだろう？ナセルと姉さんは恋仲なのだ。３、４年前に彼が恥ずかしそうに打ち明けたのを、ちょうど１階へ降りてくるタイミングと重なって聞いてしまったのだ。けど別に驚きはあまりなかった。それよりもむしろ応援している。姉さんも彼のことは気に入っていたようだし、中々似合いなのではないのだろうか。

　片付けを終わらせたのは予想通りの時間。本当にあと少し、整理した箱を押し入れにしまい床を掃除する程度だったのだから。もう少し何かで暇を潰そうとして、階下から声が聞こえた。
　「ヴァレン、かい物行ってきてくれる？」
　降りていって、もうナセルは帰ったのかと聞けば姉は静かに頷いた。
　「用事を思い出しちゃったみたい。」
　全く、暇だからやってきたのではなかったのか。彼のその気まぐれさというべきか、計画性のなさは相変わらず。
　鞄とメモを受け取って靴を履き、行ってきますと扉を開けた。道中紙を見下ろせば食材がいくつか書かれていた。…うん、これなら店道で揃いそうだ。

　大通りへ差し掛かる。歩く人影が増えてきた。かい物へ行く人、散歩する人、誰かと待ち合わせている人…今日も今日とて平和だなぁ、と足取りも軽くなる。と、
　青い柱の街灯の下に見覚えのある姿を見つける。様子から何かを待っているようだった。
　「ナセル。」僕はそう声をかけたが人混みに紛れて届いていないようであった。まだ昼頃であるし、家からさほど距離はない…目的の店が並ぶ道とは別へ通じる前ではあったけれど、彼のもとへと駆けていった。
　が。
　僕より早く、彼のもとへ少女が現れる。
　―？
　茶髪をくるりと内へ巻いて正面へ出し、フリルをあしらいながらもどこか控えめな服を着た女性に、顔の見える距離ではあったけれど、見覚えがなかった。顔も色合いも似ていないから親族ではないだろう、友達だろうか。昔から知っているとはいえ、別に毎日顔を会わせているわけではない。彼はとても気まぐれで、予告なしに遊びに来るし、気分が乗らないと約束も無駄にする。彼女が僕の知らぬ別の友達であってもおかしくはない。―なのに、とても嫌な予感がした。
　彼らは笑い合って何事かを話していた。雑踏で聞こえはしなかったがおそらく決まり文句の待ち時間問答だろう。すぐに終わらせて近くの通りへと歩き出していく。
　――――あ！
　少女は何の迷いもなく、ナセルの腕を自らの両腕で抱きしめるようにくっつくと、顔もこてりと彼の胸へ寄せた。彼は嫌がる素振りも見せない。
　理解が追いつかずに自然と首が横へ折れる。何度も瞬きしてようやく、彼らがすでに立ち去ってしまったことを認識した。

　「ヴァレン？どうかしたの。」
　呼びかけに顔を上げたものの、僕の思考は相変わらず先ほど目にしてしまった光景にとらわれていた。
　姉さんには、用事があるだのと説明しておいて、その裏では別の人間と会っていた。急に呼び出しがあったのかは知らないが、彼らの仲は、おそらく「友人」ではない。
　「ヴァレン！」
　どんと肩に衝撃が走る。タナが自分を揺らしたようだ。
　「あ…ごめん、何か用？」
　「箸、進んでないわよ？どうしたの、一体。」
　見渡せば僕の方の食器にばかり食べ物が残っている。遅めの昼食の最中であった。
　「い、いや…何でもないよ。」
　「美味しくなかったかしら。」
　「そんなことないってっ！ちょっと、考え事していただけで…」
　先程のことは…言った方が良いのだろうか。料理を詰め込むように口に入れながらまた、そんなことを考える。
　姉さんは立ち上がると食事の片付けを始めた。鼻歌まじりに食事器具を片していく。そんな後ろ姿を見ていたら、黙っている方がよっぽど悲しみを生む気がして。
　「あのさ、姉さん。」
　「なあに？」
　彼女は振り向かずに言葉だけで答える。
　「ナセルとは…どう？」
　「どうって…何、いきなり…」
　しかしそこまで言うと、何かに気づいたように顔をかすかに上げた。
　「午前中のこと？別に喧嘩したわけじゃないわよ。」
　「え？いや、そうじゃなくて。」
　僕は一呼吸置いて、先ほど彼を見たのだと話した。姉さんより年下の少女と、仲良く歩いていたと。話の途中でちらとだけ彼女の様子をうかがったが、変わらず食器を洗っている。驚いた様子も怒っている様子もなく、まるで世間話をしているだけのように。
　「…気にならないの。」
　蛇口が閉められ、水流の音が消えていく。
　「別人だと思うわ。」
　姉さんは振り向くと、にっこりとそう告げた。
　「けど…」
　「別の人よ。…あなたが気にする必要なんてないの。さ、午後は何をしましょうか…」
　そう言い残して、彼女は自室の扉を開けた。僕はもう一度思い出して、そんなことはないと思うけど、と聞こえないようにつぶやいた。

　少し離れた空き地に、遊劇団がやってきたから遊びに行こうと言い出したのはいつものごとく彼であったが、約束を当日になって忘れてサボるのも、相変わらず彼の方であった。
　「全く…」
　「仕方がないわよ。急に何かあったのかもしれないし。」
　「もう、姉さんも甘いんだよ。今の世の中だからいいものの…」
　姉さんが笑ってすましてしまうのもいつもの…というよりは、ここ2，3年であろうか。以前は彼女とて約束を頻繁に破棄する彼を、そういう人物であると理解しながらも注意していた。ところがここ最近はあきらめたのか、彼に彼女が合わせることが圧倒的に多い。
　結局始まってしまった演技を姉弟で見終わり、あれがよかったとかここはわからなかっただとか感想を述べあって帰路につく。途中のどが渇いたなと僕だけ喫茶店へ足を運び、姉さんは家へと帰っていった。
　ふう、と一息つく。HC-Z-497が店を走り回っていた。僕はニルで抽出したと書かれた特性ジョフを頼む。やがて湯気が香りをカップから天井へと持ち上げながら運ばれてきた。なるほどここの主人は接客よりも厨房に力を入れているだけはある。そこまで詳しいわけではないけれど飲みやすさが機械の作るそれとは格段に違って、ニルへ葉が溶け出したみたいに、それらが一つのものとして存在を確立しているのが初めてわかったような気がした。
　そんな風に昼下がりを楽しんで、通りのよく見える大きな窓に何気なく視線を移す。
　ひらりと、花びらが舞った。
　いいや違う。花ではない。通りを歩く人々の群れに、風になびくスカートを見たのだ。その主は誰かと楽しそうに歩いている。相手の方に見覚えはなかった。
　けれどもその"彼女"は、間違いなくナセルであった――

　「だから、悪かったって！！」
　黒にも近い木造の建物は、彼とはよく行く店の一つであった。夕方からのみではあるが酒を頼むこともできる。
　「ふーん、どうせ反省してないんでしょ？」
　「ぐ…反省なんかしたって、どうせ次もやるし…」
　「だからそれを反省してないって言うんだって…」
　全く…どんな言い訳だ。ため息をついて、僕はまだ良いけどと付け加える。
　「姉さんには迷惑かけないでよね。」
　「わーってるけど…」
　彼は運ばれてきたカップをぐいと飲み干す。
　「しゃーねーだろ…寝てたんだから。」
　「アラームかけなかったの？」
　「どうせ聞きゃしねーからなー」
　本当に反省していないのだろう、にこにことそんな調子で続ける彼に、長いつきあいではあるけど、改めて呆れる。素直なのかひねくれているのか…それとも本音を隠すため、だろうか。
　「ねえ、ナセル。本当のことを言ってよ。」
　「ん？」
　彼は寝ていたと言ったけれど、僕はその日に歩いている彼を見かけているのだ。それも…
　「知られるのが嫌なのかもしれないけどさ、嘘つく方が良くないと思う。僕、見たんだよね。」
　「お…おいおい、何の話だ？」
　彼は額に汗を浮かべて苦笑いした。
　「ちょっと前に…ほら、片付けをしているって言った日と、遊撃団のとき。君を見たんだよ。…姉さんじゃない人と、そういう風に仲良く歩いている君をさ…」
　思い出して腹が立ってきた。コップに一口つけて自分を落ち着かせる。彼を伺えば、困惑したような、冷めたような、なんともとれて何もわからない表情をしていた。
　「……」
　「…ナセル？」
　「……悪い、全く、ワカラン。」
　指を折って何かを数え始め、時折物を動かすような仕草を取ったがすぐに頭を抱えると観念したようにこちらへ謝ってきた。けれども僕の期待していた謝罪ではなかった。
　「あー…わかんねえ。ごめんヴァレン、よくわかんねーわ…たぶん寝てたと思うけど、寝てる自分が自分で寝てるなってどうしたらわかるんだ？それにあの日だろ？あの後、どうしたんだっけ…」
　「ちょ、ちょっと落ち着きなよ。意味わからないこと言ってるよ。」
　彼があまり記憶力の良い方ではないことも、そもそも行動にほとんど意味を持たせないことも知っている。だから本当は、見かけたあのときに声をかければ良かったのだろうけど。驚きのあまり―憤りや悲しさも混じって―行動に移すまで考えが回らなかった。間接的な連絡手段という手もあったが、内容が内容なのではぐらかされないか心配であったし、できればこうして姉さんにはすぐに聞かせないような機会をうかがっていたら、日が過ぎてしまった。
　「ほら、茶髪で巻き毛の女の子とか、後ろをちょこっと縛った男性に心当たりないの？」
　「えー…知らねーぞ…？…つか待て、え、男…？」
　僕はこくりと頷く。
　「いや、別に…そういうの趣味だって聞いても引かないからさ、正直に話してよ。」
　「い、いみわかりません…」
　「君女装してその人と会っていたでしょ？」
　彼は持ち上げたコップを、そのことを忘れたように落とすが、それが平たい板に打ち付けられる前に反対の手で受け止める。
　「はあ？！なんだよ、それ…」
　「わけわからなかったのは僕の方だよ！」
　しん…とあたりが静まる。店には他にも客はいたけれど、僕の出した大声で周りの人々はこちらを黙って見つめてしまったためであった。こほんとひとつ咳払いをし、何でもないように装うとやがて興味を失った見物人は各々の会話へと戻っていった。
　「…本当に、俺は何も知らねーぞ…？」
　ナセルはその白い瞳を伏せると黙したまま席を立ち、帰って行ってしまった。それがただ理不尽な証言への怒りに気分を悪くしただけなら人違いなのだろう。僕には、どちらとも判別がつかなかった。
　
　翌朝、昨日の喧嘩で沈んだままの気分を引きずりながら、そんな空気を吹き飛ばすためもかねて散歩へと出かけた。長年つきあいがありながら、他人とはこうもわからないものなのであろうか。彼は嘘をつくとか、そういう曲がったことが嫌いというか苦手というか…平気でするような人間ではないと思っていたのに。
　前方に花を扱っている店が見えた。店先に旬のものが店内へ誘うようにハの字に並んで、僕はそれを膝に手をついて眺めてみた。色とりどりの花や草が、それでも秩序を持ってそよ風に揺れている。と、
　「いちいち関わってくるな！」
　罵声に驚いて顔を上げる。店の入り口から青年と少女が飛び出し、内部から反論の声が発される。2人は不機嫌顔を道へ向けるとそのまま足早に去って行ってしまう。それが"赤の他人"であればただ呆れるばかりで、店内の人の愚痴でも聞いたのだろうが。
　僕は駆けていった。白髪の青年と、緩くまとめた茶髪の少女。紛れもなく彼らはあの日、あの通りで見かけた彼らであったのだ。
　追いかけていって、言葉じゃ届かないかもしれないと彼らの前に立ちはだかるように、しっかりと体を支えるように足を広げた。
　「……？」
　僕が息を整えている間に彼らは立ち止まって、何だろうと言わんばかりに顔を見合わせる。少女が声をあげた。
　「あのー…何か用？」
　キッとナセルを睨む。彼は涼しそうに、つまらなさそうに見つめ返していた。
　「…もう、言い逃れはできないよね？」
　そんな彼の態度に腹が立って、つい声に怒りが漏れる。少女の方が不快な顔をして、彼に知り合いかと尋ねていた。
　「…さあね、どうでもいいじゃん。行こ。」
　「なっ……」
　どうでもいいだって？
　顔を背けて歩みを進めようとするナセルの腕を掴み、彼らのつないでいた手を引きはがす。そうしてようやく彼は、僕へと敵意を向けた。
　「…はじめまして。」
　僕はぱっと彼の腕を放すと、少女へ向き直り軽くお辞儀をしてみせる。予想していなかったのだろう、きょとんと目を丸め、離された腕を反対の手で覆う。
　「僕は彼の…恋人の弟です。」
　「はあ？」
　後ろにトゲのある声が聞こえた。一方の少女は、首をかしげて的外れな悩みをつぶやいていた。
　「えーと…私、ひとりっ子だよ？」
　がくりと足を踏み外してしまいそうだ。わざとなのか本気なのかは知らないが、ひとまずそうではないと首を振って回りくどい言い方はやめにした。
　「ですから、あなた以外に彼には恋人がいるって事ですよっ」
　「…えぇ？！」
　少女は一瞬間をあけ驚くと、僕の後ろへ見える彼へと視線を向け、僕もそれを追いかけた。彼は頭をかいてため息をつく。しかし焦りはうかがえなかった。
　「…で、おまえの目的は？」
　「え？」
　聞き返すと彼はだから、と説明を加える。
　「そんな嘘で得たいものは何だ？ただの好奇心か、それともそいつか…」
　「…本気で言ってるの？」
　彼は当たり前のように頷き、納得したように少女もぽんと手をたたいた。どうやら彼らは、僕がいたずらに道行く人への迷惑行為をしている少年か、彼女の追っかけのどちらかだと決めつけてしまったらしい。
　「…どうしてだよ、ナセル。」
　どうして？あのときだって姉さんと仲良く話し始めたじゃないか。用事とか適当なこと言って別の人間と過ごしているなんて…僕は呆れやら悲しみやらで項垂れた。
　「…『ナセル』？おい、なんだよそりゃ…」
　彼の声に顔を上げる。彼と彼女とを交互に見つめ、その不思議そうな表情に、背を汗が流れたのを感じる。どうして、は僕の方だ。その可能性を受け入れなかった。それは彼の容姿がひどく珍しいためであろう。

　「ごめんなさい！」
　これでもかと腰を曲げ、僕は全力で謝った。
　「なーんか、変だと思った。話がかみ合ってないみたいにさ。」
　少女、カルーンは納得がいくとナセルにそっくりな青年、ハウェアの隣へと並ぶ。僕はもう一度顔を上げ、申し訳なくて恥ずかしくて俯く。
　「…そんなに似てるのかねぇ……」
　ハウェアは幾分か人違いに気分を悪くしているようであったが、少女の方は温厚なのか少し驚くだけで声音から怒りはうかがえない。
　「それは、もう…近くで見たって、違いが判らないくらい。そりゃ、比べれば違うんでしょうけど…」
　違うかな、と思うところは演技でまかなえるようなものばかり。髪の色、目の色、声音―ただしこれはハウェアの方が乱暴な調子を含んでいる―、背格好…何から何までが同じに見えてしまう。
　「じゃ、一件落着ってことで…ハウェアも沸点低いけど、あなたも気を付けてね。ばいばーい。」
　言ってカルーンはハウェアを引っ張っていった。僕はもう一度お辞儀をして、そんな彼らを見送った。
　
　穏やかな日差しにあくびをして、郵便受けを確認する。配達物は特になし。情報誌や広告の類いも特には入っていなかった。というのも、僕の家は街から少し外れにあって、別段生活がかかっているわけでもない彼らがそこまで赴く必要もないためである。
　見渡せば街へと続く一本道の周りの草原で草花が揺れていた。
　「もし。」
　そんなのどかな風景にふさわしい気高くも優しい声がかけられた。振り返ると、少女とおぼしき2人の人影があった。
　「はい…？」
　ブロンドの巻き髪を両サイドにまとめている少女の方が日傘を傾けてにこりと顔を見せた。
　「ここらでクワルを見かけませんでした？首にカイのプレートをつけていて…」
　「クワル、ですか…？」
　プレートの有無を考慮しないにしても、クワルなどと珍しいものをみつけたらちょっとした騒ぎになる。けれどもそんな噂は聞かないし…
　「いえ、知りませんけど。」
　それにしても物好きな子だ。見た目からも"お嬢様"をにおわせるが、何かしらの分野で名のある者なのだろうか。なんとなく、クワルやそれに類するものにはそんなイメージがある。
　「もう、あの子ったら一体どこへ行ってしまったのかしら…」
　少女は少し頬を膨らませて街を向く。すると彼女の後方から、さきほどまで目立たないよう控えていた付き人らしき少女が声を上げた。
　「あ…あの、ラック・ローズさま…」
　ハンチングを目深に被り、ささやくような小声であった。
　「ん？どうかした？」
　「やはり、お屋敷の方ではないでしょうか…きっと噂になってしまいます。」
　それもそうね、と日傘の少女は納得したように頷いていたが、僕としては理解できる状況ではなかった。
　―だってその声は…
　そっと、顔を覗くため膝を曲げる。帽子の唾から短い白髪とまっ白な瞳が「ラック・ローズ」と呼ばれた少女へ向けられている。
　僕は思わず手を伸ばし、その帽子へ触れるとつまみ上げて取り去った。驚いた彼女たちは目を丸くしたが、すぐに強気な叫びが突きつけられた。
　「何するのよあなた！」
　手のひらの帽子をラックが取り返す。彼女はすぐにもう1人へ帽子を戻すと、かばうように自分の体を僕とその子の間へと入れた。彼は後ろで震えていた。
　「……どういうこと？」
　「それはこっちの台詞です。いきなり見ず知らずの人から物をとるなんて…」
　「見ず知らず…ではないんです。僕は彼を…知っている。」
　「え…」
　短く疑問の声を上げたのはラックではなかった。
　「そうだよね。」
　「し、知りません…わたしは…」
　はあ、とラックがため息をつく。
　「相手しなくて良いわよ、ロトルア。あなた何ですの？この子は渡しませんわ。」
　「ふざけないでよ、ナセル…」
　それまでは僕を誰かの仇のように鋭い目つきでにらみつけていたラックだったが、何かに気づいたようにはっとすると1人頷き、怯えるような彼へ待っているよう告げて、僕の腕を引っ張って家の側面へと回り込んだ。
　少女はぱっとスカートの汚れを払うような仕草をして、
　「ごめんなさい、私ったらてっきり口説いているのかと…"タナ"さんですよね？初めまして、ラック・ローズと申します。お話はカルーンさんから伺っておりますわ。」
　「わ、ち、違います…タナは僕の姉です。」
　「え…？」
　え、と驚きを示したいのはこちらの方だ。明らかにモノ探しに来ただけの少女から、まさか姉の名が出ようとは…
　ラックはしばらく黙り込んだが、やがて先ほどの敵意も謝った時のようなうれしそうな顔もなくなった、微妙な表情となった。
　「…無関係者？弟さんは、知らない…？」
　「何のことですか…」
　「確かに、知っているにしてはおかしな物言いでしたけど…」
　聞き返したことにも気づかずに、ラックは自らの思考を続ける。僕には彼女のつぶやきがまるで理解できなかった。彼女の言葉に出てくる単語はどれも理解できるのに、それらが一つの文へと形を成すとわからなくなる。最も重要な情報がぬけているのだろう。
　「あ、あの…どういうことか教えてくれませんか？」
　少し声を張り、もう一度声をかける。彼女ははっとしてこちらへと向き直るとただ静かに首を振った。大鳥のように優雅に、演技なのかはさておき変わらずの不安そうな表情の彼の元へと歩き戻っていった。
　「行きますわよ、ロトルア。」
　「はい。」
　待ってくれと、教えて欲しいと改めて頼んだものの、ラックが一瞬だけこちらへお辞儀しただけで、彼女たちは去って行ってしまった。
　怒りにも悲しみにも空しさにも、呆れにさえならないもやもやした感情を持て余して、ひとつため息をついた。
　
　すっきりと解決したはずの事柄に、再び靄がかかった。他人は他人、関係がないといえばまあ、それまでなのだが両親を早くに亡くしたために生活の知恵を与えてくれた姉は僕にとっては大切な人なのだ。無駄な心配も悲しみもできうる限りは避けたい。…いや、そんな綺麗な事ではなく、親友…と思っていた人物の謎多き言動。裏切られたと認めたくなくての探索なのかもしれない。
　寂れた道へ向かっていった影をみつけて後へついて行ってみれば、そこは行ったこともないような場所で、すぐ近くにこのような並木道があったのかと本来の目的を忘れそうになる。
　怪しまれないように、かといって顔がばれないように距離を置いて帽子をかぶり、のそのそと歩みを進める。彼は周りを気にした風もなくまっすぐに道をたどっていく。前方から通行人が現れても、それが、以前に会っていた「彼」であっても。
　「って、えぇ？！」
　思わず声に出た驚きをふさごうと口に手を伸ばす。不審な動きにこちらへ視線を向けたのはその、喫茶店から見てしまった、ナセルがフリルを身につけていた時一緒にいた男であった。彼の方も、奇声を上げた僕へ侮蔑の視線を向けることはあっても、ナセルへ興味を示す事はなかった。何か、別の利益があっての関係だったのだろうか。
　やがてナセルは一つの家の前で立ち止まると、扉をノックした。僕は慌ててすぐ近くの木の後ろへ背を預けた。
　少しの間の後に現れたのは紫髪を短めに切りそろえた少女で、カルーンでもラックでもなかった。
　また、新しい人か―
　自然とため息が漏れる。重心を傾けてそちらへ足を伸ばしたが、彼らはすぐに家の中へと入っていってしまった。さすがに"彼女"の家へと押し入るわけにも行かずに半端な位置で突っ立ってしまう。
　「何してるの？」
　声に振り向くとカルーンがいた。かい物帰りなのだろうか、手提げの籠には材料袋が詰め込まれている。
　「いや、なんというか…」
　「元気ないね。あ、そうだ…」
　言うと彼女は僕が来た道へと2，3歩進み手招きする。
　「店に寄ってかない？ちょうど材料仕入れたし…」
　「店をやっていたんですか。」
　「うん。誰かと話すの好きなのよ私。」
　カルーンは手元の端末のカバーを開くと何が良いかと質問する。とりあえずドンサトを、と頼めばすぐに彼女はスイッチを押した。厨房の方から機械独特の動作音がする。
　「ハウェアともこの店の前で会ったんだ－。」
　え？と聞き返せばこの間一緒に歩いていた彼だと説明される。別に誰かという意味で聞き返したわけではないのだが…彼のことは、忘れようとしてもしばらくは無理であろう。
　「彼ね、あんな怒りっぽいけど結構臆病なんだよ？」
　「はぁ…」
　「それとね、ここみたいな調理機の味が好きなんだ。」
　「そうですか…て、え？」
　どうかしのかと首をかしげた彼女であったが、調理終了音が部屋に鳴り響くと、立ち上がった。はじめはなぜそのような惚気を聞かされなければならないのかと疑問であったが、それよりも彼女の言葉が気になった。たしかナセルは…
　少し前から姉さんが材料袋を使わない、素材を加工する類いの料理を始めた。少しして彼に振る舞ったことがあったが、そのとき彼は確かにいつも食べてるものより遙かにうまいと笑顔を見せていた。
　カルーンが料理を運んできた。片手には僕が頼んだドンサト、反対の手にはガンブが湯気を立てている。彼女も食事をするのだろう。
　「カルーンさん。」
　「なあに？」
　「あのときは納得したんですけど、あれから色々あって…やっぱり僕は、人違いではなかったと思うんです。」
　照明の具合も、空模様も何も変化はなかったけれど、ふっと辺りが暗くなったかのように思えた。さきほどとは打って変わってカルーンは真面目な顔つきになる。
　「…それは、ナセルさんのこと？」
　「ええ。ハウェアのことです。」
　彼女はため息をついて視線をそらす。
　「ちょっとだけ期待したんだよね、本当は。ちょっとだけだよ？…もしかしたら、知っている人なのかなーって。でも君は、『彼』の知り合いじゃなかった。」
　「どういうことです？」
　「ごめんね、関係者以外には言わないことにしているの。」
　―関係者？
　「あの、それは」
　「だーいじょうぶ。…たしか、お姉さんが心配だったんだよね？安心してよ、君の心配しているようなことは何もないから。」
　それ以上のことは、うまいこと話をそらされてのせられて聞き出すことができなかった。店には人が集まりだし、長居するのも失礼かと、そろそろ帰らなければという時間となってしまい今日のところは諦めるしかないようだ。
　カルーンさんは、帰り際にもう一度大丈夫だよ、と念を押した。

　"世の中には似た人が3人はいる"、との言葉は聞いたことがあった。
　けれども僕が出会ったのは少なくとも4人で、さらには世界どころかここら周辺にいるようなのだ。ここ最近のことだから、一部は以前から住んでいるというより遊びに来たか、近頃越してきたか…そもそも、彼の場合は同じ色合いを見つけるだけでも苦労しそうなのに、本当にそんな"何もない"ことが実現できるのだろうか。
　そんな屁理屈のような皮肉のようなことをぼーっと考えながら時間を確認する。
　小さな林に隣接した公園。子供さえ遊んでいないこんなところでやることはといえば、人を待つこと。1ラクほど前にナセルと、今日隣町で行われるイベントへ遊びに行こうと約束していた。そのときはまさかこんな感情にさいなまされるとも知らずに二つ返事で了承してしまったのだが、今回ばかりは心のどこかで彼が約束を忘れていたらいいのにと願ってしまう。
　時刻は約束の少し前を示していた。頂上へと向かう日がいつもよりまぶしく感じる。その明るさがなんだか憎たらしくて、たまには僕の方が約束を破っても良いのではないかとの考えが浮かぶ。彼も待たされる怒りを思い知るべきだ、と。
　少し悩みはしたが、そうと決めるとそのまま丘へと向かった。
　日常なら家族づれやら何やらで賑わっている草原だが、隣町でのイベントは、なかなか評判が良く多くの者が楽しみにしていることで、今日は丘の頂上を挟んで生える大きな二本木もどこか寂しげだった。
　一本の強い風が吹き抜けていく。風下へと視線を移す。遠く―そちらは確か崖となっている―に人影が見えた。
　「な…」
　こんな日に、こんな昼前の明るいときに何をするつもりだろう。僕は足早にそちらへと向かう。幻などではなく、その崖のすぐ側には人が立っていた。
　その人は遠く空を見上げていたが、足音に気づいたのかこちらへと振りかえった。

　たとえばこの草原が黄金に変わって、僕の意思を、その子の髪を靡かせる風がさらって行ってしまったような、虚無の恐怖のようでしかし無限を体感しているような心地の良い感覚にとらわれる。

　僕は、その子の無知な微笑みに心奪われていたのだ。

　それがそのような感情を抱くべき相手でないと気づいたのは、その人が笑顔を解いて不思議そうにこちらを見つめた時であった。
　「…って、ナセル？！」
　大声に驚くようにびくりと体を震わせる。そんな彼に近づいて肩をたたいた。
　「もう、こんなところで何してるのさっ…」
　そこまで言って急に罪悪感に襲われる。彼はしばらく黙っていたが、肩に置かれた僕の手にそっと触れてどかすように誘う。
　「…セントクレア。」
　透き通るような、か弱く優しい声だった。
　声質というか、根本的な音はナセルのものであったのに、最終的な音の出し方は彼のそれではない。
　彼の手が頬を包むように触れられた。そんな声色や雰囲気には似合わぬ、しっかりした手であった。
　「なせるジャナイ…セントクレア。」
　「え…？」
　喋ることになれていないのか、カタコトにそう告げると、再び手を下ろして微笑んだ。
　「セントクレア…ヴァレンスキ…」
　「はい…？」
　「キノシた…ネムッテタ…ヴァレンガ、オコシてクレタ…」
　キノシた―？木の下…？
　出会ったときのことであろうか。少し時間が欲しい。突然すぎて頭を整理したい。
　「ええと…『セントクレア』？それは一体…」
　しかし彼はそれには答えずにまっすぐにこちらを見つめていた。
　いや、僕の後ろを見つめていたらしい。そのことに気づいて振り向ききるよりはやく聞き覚えのある声が聞こえた。
　「あらあら、こんなところにいたのね。」
　「もしかして、新しい子かな？」
　「どうりで、誰とも一緒じゃなかったはずだ…」
　「ふふ、仕方のないことですわ。ねえ？」
　「え、うん…」
　複数人の集団が、覗くように二本木の影から現れる。彼も彼女も、僕はそれぞれに知っていたけれど、彼らが集っている事への説明は思いつかなかった。
　「姉さん…？！それに、カルーンさんも…」
　右の腕に何かが触れたことも気にせずに、丘の頂上の辺りへと戻り、順に顔を見回してもう一度首をかしげると、二度ほど見かけた男性が疑問の声を上げた。
　「知り合いか？」
　「ええ。紹介しますね、私の弟のヴァレン。」
　姉さんがこちらへ手のひらを地面と水平になるように向け、反射的にお辞儀をした。僕だけが一方的に見たことのある2人―疑問を投げかけた男と並木道の家の紫髪の少女―もお辞儀を返した。
　顔を上げたところで、腕の違和感に気がつく。
　「うわぁ？！」
　思いきり振り払うように退くと、彼は手を離してくれた。ナセルが、僕の腕へと抱きついていたのだ。驚くなという方が無理である。寒気さえしていた。というのに、
　「…ヴァレン、そういう態度はひどいんじゃないの？」
　「え、ええ…？」
　そう諭したのは姉さんだ。確かに彼も離された手を淋しそうにお互いで包んでうつむいているようであったが、怒りの矛先が間違っているのではないだろうか。
　「だって…ナセル、だよね？」
　確認するように、自らを落ち着かせるようにつぶやいた言葉に、その場にいた皆が首を振った。そうして彼を―彼は自分を―指さすと、声をそろえて、
　「「セントクレア。」」
　と僕へと告げた。
　そうしてまた彼が腕へとくっつくのだが、それをどうこう言う者は誰一人いなかった。ここにいる誰もが、彼とはそういう関係だと思っていたのだが、まさかまた、別人だと言い張るのだろうか。
　「あの、皆さんはどういうお知り合いなんですか？」
　「どういうって…まあ、成り行き？」
　「難しいですよね、"今"説明するのは…」
　穏やかになった風が木の葉を揺らす。側のセントクレア(？)の髪は、彼と同じく短かったのに、川が流れるように遠くまで伸びているのではと感じてしまう。続けられない言葉を待つ間に改めて見つめてしまっていた。と。
　「きゃあ！！」
　突然彼…女が僕を突き放した。あまりに突然だったから受け身もとれずに草の中へと突っ伏してしまう。何をするのだと起き上がれば、ラック・ローズが彼女をなだめていた。
　「ラックさま…変な人が…」
　「よしよし、大丈夫よ。」
　先程までの、喋り慣れていないようなカタコトさは何処へやら、儚い雰囲気と共に隠れて、彼女は臆病な少女のようであった。
　「…あ、ちょうどいいんじゃない？」
　僕の混乱を察したのか、カルーンがラックへ、彼女を落ち着かせるために少し離れたところにいてはどうかと提案する。どうやら、彼女がいるところでの説明は難しいという意味だったらしい。
　「ええ、勿論です。ほら、行きましょう？」
　「はい…」
　彼女らは崖の近く、危険でないところまで仲良く歩いて行った。そんな後ろ姿を見送って、さて、と切り出したのはカルーンだ。
　「ご覧のように、今は"ロトルア"なの。」
　「え…？」
　―ロトルア。確か以前、ラックと共にいた彼がそう呼ばれていた。
　僕はそのことを思い出し、彼女の恐れるような仕草には納得することができた。が、つい先程まではセントクレアだと言っていなかったか…？
　「『ロトルア』ちゃんはラックさんの恋人なのよ。女の子同士だけどね。」
　姉さんが微笑みながら続け、男がどこかを見つめて付け加える。
　「性別なんて、他人には関係ないだろ。」
　―いや、そうではない。
　たしかにそこへの疑問も尽きないのだが、今はとにかくそうではないのだ。
　さらに、理解の追いつかない僕へ追い打ちをかけるかのように、カルーンが得意げに説明する。
　「私の彼やタナさんの恋人は会ったことあるよね？ハウェアにナセル、それでこちらの、マナグアさんの恋人が「ノイジードル」、ガトゥンさんの彼女は「ワカティプ」っていうのよ。」
　彼女は順に、自分、姉さん、紫髪の少女、男と指し示していく。
　「ん…？」
　「だから言ったでしょ？あなたが心配するようなことは何もないの。」
　彼女は最後に僕を指さすと仕方のない…と言わんばかりに胸を張った。
　やはり、同一人物だとの認識であっているのだろうか。
　「呼び方を変えている…ってことですか？」
　「えぇ…？」
　呆れるような不審に思っているような視線が集まったのは一瞬のことで、彼らは僕の欠けている前提とでも言うべき条件をすぐに察した。
　「あ、だからね、あの子は多重人格者なの。」

　僕は二度瞬きをして、自分の中の辞書をたどる。そうして見つけたその意味と、今までとを照らし合わせ、ようやく現実を理解できたのだ。
　意味のわからなかったことが明確になった瞬間というものは、一種の快感を持っている。
　「…て、え？！ナセルが？！」
　「そりゃそうか、わかんないよね。」
　「私も聞くまで疑ってましたし…」
　「ああ、タナさんは本当にすごいよな。」
　彼らが言うには、はじめにそのことに気づいたのは姉さんだという。「ナセル」を始めとする彼ら自身でさえ認識していないことであった。そうであったのに、彼女は嘘と決めつけずにカルーンさんやガトゥンさんと話し合って事実を突き詰めたそうだ。それからは新しくできた恋人へも説明し、今の奇妙な関係ができあがっていった。
　はじめてラックと会った時に、彼女が"カルーンから聞いている"といったのはそのことだったのだろう。
　「皆それぞれ知り合いでもなかったけれど、いい人達できちんとわかってくれたのよ。」
　「ま、それぞれあの子が気に入ったんだから、些細な事よね。」
　彼らは彼…ナセルやらセントクレアやらをまとめて呼ぶ時は「ラーセ」という言葉を使っているそうだ。たしか、古い言葉で「総称」を意味する語だと昔姉さんが教えてくれた気がする。その「ラーセ」は誰も自分以外を知らない。先程理不尽に「ロトルア」から突き飛ばされたのも、そういうことだったのか…
　「…いやいや、どうしてそんな簡単に受け入れられるのさ！だって"見かけ"は同一人物なんだよね？！」
　出かけに行く程度ならさほどの問題はないだろう。けれども…例えば結婚するだとか、もっと深い関係となった時に、「別の人格だから」で妥協できるものなのだろうか。
　「え…できないの？」
　しかしその問いにはきょとんと目を丸められる。―あるいは、そこまでは考えていないのだろうか。それとも僕が変なところにこだわりすぎているだけなのだろうか。彼らの団結力はそう錯覚させるほど強く、"妥協"という言葉さえ似合ってはくれなかった。
　頭を冷やそうと遠くへ顔を向ける。が、冷えるどころかまさに今心配していたようなことが行われており、パニックで不自然に立ちはだかったため、彼らもその光景を目の当たりにした。
　「あらあら。」
　「全く、いくら人目がないからって、相変わらず積極的なやつだな。」
　「あはは、ラックさんはまっすぐだよね。」
　「わあ…」
　…が、反応はこの通りである。マナグアに至っては、彼女の方が恥ずかしがってしまう始末。怒りとか嫉妬とか、彼らにはないのだろうか。
　「あの…自分の恋人にキス…されているんですよ？」
　「え？ロトルアだよ？」
　「だいたいそれがなんだって言うんだ？」
　話が通じない。
　僕はなんだか疲れて、諦めてそれ以上そのことについては触れないでおこうと一人思う。
　「それよりヴァレン、どうするの？セントクレアちゃんのこと。」
　「え…？」
　いきなり何のことやら、と姉さんへと向く。
　「ほら、あの子はどうみてもお前に気があっただろ？きちんと応えてやれよ。」
　「ま、嫌だっていうのも伝えないとわからないからねー。」
　ああそういえば、というのが素直な感想だった。とはいえそういうことに慣れているわけでも、彼…女に全く興味がないわけでもない。
　ただ少し、事情が複雑だったのだ。
　僕にとっては長い間ただ彼であったのだから。
　明確に答えも出せずに考え込んでいると、ラック達が戻ってきて話へ加わった。
　「まあ、すぐにどうこうする必要はないんじゃないかしら。次いつ会えるのかもわかりませんし…」
　「え…？」
　一周頭を回してからその答えにたどり着く。ロトルアは話について行けずに相づちとしては不自然に首を動かすのだが、ラックを信頼しているのか深く関わろうとはしなかった。
　「…あ、そっか、そうなるのか。」
　カルーンがちょいと手招きするので耳を貸すと補足してくれた。
　「…だからね、時間を指定するような約束事も禁止。したいのにできなくて、ストレスになっちゃうことあるから。…まあ、気にしない人もいるみたいだけどね。」
　ぱっとある人物のことが思い当たる。口先だけは謝っても、何の対処も解決も考えようとせずに笑っている。
　―そうか、彼がよく約束を破っていたのは。ある時から、姉さんが注意しなくなったのは。
　僕が頷けば、周りの皆はにこりと笑った。
　「そうだ。これからみんなで、隣町で行われている《ショヤン》に行きませんか。」
　彼らはとても仲が良い。友人グループ…というよりは、結婚相手を含んだ親族のような関係なのだろう。
　反対する者は誰一人いなかった。もちろん「彼女」も。
　僕もこの奇妙な関係に加わるのかと思うと、嬉しいような、悲しいような…`
