export const novel = `プロローグ 発見
　人は決して立ち入ってはならぬ領域と聞いたのは、その幼子を付近で見つけた後であった。かつて武術において名を馳せ、周りからの声に自惚れていたこともあったが、愛するものを見つけると途端に名声などどうでもよくなった。そうして数日前に妻や子と小さな村へ越してきたばかりで、妻は生まれ故郷とのことで知っていたのだろうが、言う機会もなかったという。
　私はその幼子を育てることにした。生体情報としての繋がりはなくとも、妻も子も喜んで迎え入れた。年の近い子供らと遊ぶようになり、兄に影響されたか武術を習いたいと張り切り、家族として過ごしてきた。
　そうしてこのまま、村で平和に暮らすのだと…`