































import {Vue, Component} from 'vue-property-decorator'

@Component
export default class TopPage extends Vue{
  private titleAnime = false;
  private imageSrc = require('@/assets/top.png');
  private updates = [
    {title:'漫画UI変更', date:'2020/04/12'},
    {title: 'リンク追加 & 水晶交渉After up', date: '2020/04/05'},
    {title:'キミノシ 2話up', date:'2020/02/24'},
    {title:'蒼なる静夢 裏 追加', date:'2020/02/22'},
    {title:'About調整, タイトル画移動', date:'2020/02/22'},
    {title:'TOP画 変更(全4種), UI調整', date:'2020/02/11'},
    {title:'イラスト付き小説「森ノ神様」開始', date:'2020/02/11'},
    {title:'鸚緑の仮面 2 up, 微調整', date:'2020/01/15'},
    {title:'イメージイラストup', date:'2019/09/29'},
    {title:'サイト移転', date:'2019/09/16'}
  ];

  mounted(){
    this.titleAnime=true;
  }
  beforeMount(){
    const random = Math.floor(Math.random() * 4);
    switch(random){
      case 1: this.imageSrc = require('@/assets/top3.png');break;
      case 2: this.imageSrc = require('@/assets/top2.png');break;
      case 3: this.imageSrc = require('@/assets/top4.png');break;
      default: this.imageSrc = require('@/assets/top.png')
    }
  }
  
}
