
























import {Vue, Component, Prop, Watch} from 'vue-property-decorator';
import TemplateListOnly, {listItemType} from '@/components/template-list-only.vue'

export type itemType = {
  title: string,
  show: boolean,
  novel: string,
  rb: string,
  isNovel: boolean
}|{
  title: string;
  show: boolean;
  icon: any;
  src:any;
  text: string;
};

@Component({
  components:{
    TemplateListOnly
  }
})
export default class TemplateListWithDialog extends Vue{
  @Prop()
  items!: itemType[];
  @Prop()
  dark = false;
  @Prop()
  isInside = false;
  @Prop()
  isVertical = false;

  private titleSuffix = "　終";
  private listTitles:listItemType[] = []

  mounted(){
    this.setListTitles();
  }
  clickList(index: number){
    this.items[index].show = !this.items[index].show
  }

  @Watch('items')
  setListTitles(){
    this.listTitles = [];
    for(const item of this.items){
      const tmp: listItemType = {title:"", icon: ""};
      tmp.title = item.title;
      if('icon' in item){
        tmp.icon = item.icon;
      }
      this.listTitles.push(tmp);
    }
  }
}

