　「…何があったのだ？」
　従者が顔を出す頃にはセーレンは消え、乱れたクノーテンが横たわっているのみであった。別にタイミングがよかった訳ではない。なんとなく、ここへ来る気乗りがしなかったのだ。それが魔法によるものだと彼は夢にも思うまい。
　「おい、ツェルクーペ。何があったか報告しろ。」
