　「え…？」
　また忘れちゃったの？とセーレンも首をかしげたが、クノーテンは何が何やらさっぱりであった。
　「そっか…先を知る恐怖もなかなか良かったのにな。」
　独り言へ侮蔑の視線を向け、スフォリアはクノーテンへ質問を続ける。
　「先ほどのことは覚えているか？」
　「えと…？」
　少年は視線をあちこちにやって必死に思い出そうとする。
　「…お前は、先程一度シんだのだ。」
　「え…？」
　スフォリアは言うとすぐにセーレンへと向き直る。彼もまたつまらなさそうに睨み返した。
