　「また、会えるかしら。」
　「さあ…はあ。一体何人いるのやら…」
　
　気がつけば夜道を歩いていた。ここ最近は少し多いような気がする。あの気分屋にも、それなりに悩みがあるのだろうか。それにしてもまたこの少年と遊んでいるとは…仲がよろしくて何よりだ。
　「あ、ねえナセル。この前貸してくれた本だけど…」
