　「…何、言ってんだよ…ナセル、だろ。」
　僅かな欲が許してくれなかった。
　彼女はその答えに驚きの声をあげる。
　彼は彼女のその疑問に疑問をもつ。暫しの沈黙。
　「…あなた、もしかしてナセル君を知ってるの？」
　一体何をとぼけているのだとの思いとは別のところで、感づかれているのではと不安になる。原因があるとしたら、この間の失敗だろう。
