　けれども引くわけには…
　「…いいえ、あなたは《ナセル》君じゃないわ。」
　タナのその言葉で、彼は初めて口以外を動かした。両手で耳を覆うように屈んだのだ。閉じた隙間からくぐもった心配の音が聞こえる。
　―悪い、ナセル…
