　最後に一目だけ、と一瞬顔をあげる。もう出ない。彼女の前には、絶対に…
　「…また、会いましょうね。」
　そう、誓おうと思ったのに。
　最後に見た彼女は、その言葉と共に微笑んでいた。
　
　結局、名前さえ知ることは叶わなかった。
　目の前で再び眠りについた青年を見つめて、タナはひとつため息をついた。
