export const novel = `　「彼」だけがその場で静かに微笑んでいた。
　風が、それまでの蟠りを、悩みを不安を吹き飛ばすように入ってきては出、彼の髪を揺らしていた。部屋には無数の紙と、小さなガラス細工が転がりその場の異常さを演出する。
　グラナートもシアンもサラテリも、扉を開けた勢いを忘れて、事態も掴めずに唖然とする他なかった。
　ただ、そこには「彼」がいただけであったのだが…

プロローグ.　ウェルド

　彼は、どこにでもいる普通の青年であった。
　勉強はあまりできないが運動神経はそこそこ、特にトウ術においてはそれなりの才能を持ち、父と妹との3人暮らし。歳の違う親友がおり、仲の良いクラスメートがおり、人並みな恋心も持ち合わせている。本当にどこにでもいる、「普通の」人物だった。
　終業を知らせる音が鳴り響くと、すぐにそのようなものは欠き消されるほどの喧騒に包まれる。きょうは少しだけ特別だったこともあり、一層辺りは騒がしかった。
　ウェルドは紙の束を整えて鞄へ雑に詰めていく。別段怒っているわけでも急いでいるわけでもなく、単純に彼がそこまで几帳面な性格をしていないからである。そうして帰宅準備を進めていると、前の席の人物が身を乗り出してきた。
　「きょうバハラに行かないか？」
　ヴァイナスとウェルドはスクイルに入ってから妙に馬があってよくつるんでいた。問われて青年は少し考える。きょうは夏の丱のちょうど真ん中。だからベクは禁止されていて、少し早く帰宅できる。彼のように友人と遊ぶもよし、家で孝行するもよし。
　「んー…悪い、やめとく。」
　返事をもらうとそうか、と彼は別の友人を誘いに席を離れていった。周りはこんなにも賑やかなのに、なんだかそんな生活音を永遠聞かされているだけ、取り残されているようだった。
　スクイルと道との境に差し掛かる頃であった。後ろから知性と好奇心の混ざった声がかかる。振り向くと親友のシアンの姿があった。
　「やあ、真っ直ぐ帰るのかい？」
　立ち止まった足を再び元の方向へ戻せば、シアンも隣へ並び歩き出す。
　「まあ、特にすることもないし…」
　予定も計画もない。断ったのはただの気まぐれだ。気分じゃない。その程度の理由だった。
　「じゃあさ、家で遊ぼうよ。…と、その前に買い物頼まれてるんだった。」
　「いいぜ、付き合うさ。」
　彼らはそのまま真っ直ぐ道を辿っていく。ばらばらと周りの生徒達が側を歩いていた。
　ついこの間2の灯があったばかりだというのに、少し早いだけの終業に、何をそんなに浮かれるのだろうか。そんなことを考えていれば、やがて道には樹木が現れる。それらが深くなるより手前で十字路を曲がれば、小店の並ぶところへ出る。スクイルから反対側へ向かえば、それこそハバラバもあるほど栄えた通りに行けるのだが、帰り道の買い物には適していない。
　ふっと目前が白に溢れる。
　「ウェルド？どうかした？」
　気がつけば十字路の真ん中で立ち止まっていた。
　「んいや？…ちょっとぼーっとしてただけだ。で、何が必要なんだよ？」
　「常用品だよ。雑貨屋にいけば揃うと思う。」
　
　雑貨屋の店主はもの好きで、必要な品からそうでない、何に使えるのかさえわからぬ品まで、小さな店に閉じ込めていた。シアンが買うべき品定めをしている間、ウェルドは店の奥へと足を運ぶ。それはセンスもない飾りであったり、蛇足にまみれた道具だったりと様々で、呆れが顔から剥がせない。
　そんな中、ふと目に留まったのは液体をいれる容器であった。所謂カップにしては分厚くてそこも深い。けれども最も異様なことは、その持ち手と思われる場所についている、おそらく2つのカテンであろう装飾であった。上体はよく絵に描かれる二枚羽の人間に近い姿なのだが、下部で2つなのか1つなのかが判別つかなくなる程自然に"繋がって"いた。青年はぎくりとして虹彩を震わせたが、しかしどうしたことか顔を背けることができなかった。
　「お待たせ。ほら、行こう。」
　少し離れて聞こえた呼び声で我に返る。
　「…あ、ああ。って、結構な量だな。」
　会計場所まで戻っていけば、両手に大荷物を抱えたシアンがいた。数もそうだが、大きな物が占めているという印象だった。
　「でしょ？だから姉さんには行かせたくなくて。」
　シアンの義姉、ヴェローナの姿を思い浮かべ、なるほどとひとつ頷き、先ほどの感情は全て消え去ってしまった。ウェルドの恋心とはつまりはそういうことなのだが、彼女の方はシアン同様の弟のように感じているのではないだろうか。
　店主がひとつあくびをして、からりと扉の鐘が鳴った。
　
　やる気のない、というよりは当然と馴染みすぎてもはや無意識の挨拶が開閉音の後に聞こえると、食事の支度をしていたグラナートは手を止め、部屋と廊下の敷居から玄関を覗く。そうしてやって来たウェルドを迎え、もう少しでできるからと皿を取り出した。
　「親父、きょうは？」
　「父さんならゴウシだよ。泊まりだって。」
　「ふーん、綸段ってのは相変わらず忙しいな。」
　ウェルドとグラナートの父、カテドラルは仕事で家にいることは少ない。それなのにと珍しくその所在を尋ねた兄に、何か用があったのかとグラナートは後ろで纏めていた二段縛りの下をほどきながら尋ねる。答えは「別に」とその話は終わったかのように素っ気ないものであった。
　食事が終わると、二人は居間で映画を見る。グラナートが借りたものらしい。彼女はこの手の、夢見がちな恋愛劇には興味がないものとばかり思っていたのだが…と気になったウェルドは問いかけた。
　「お前、こういうの好きだっけ？」
　「うーん…好きか嫌いかは難しいな。でも、シエナが好きだからさ。見ておけば話せるでしょう？」
　少女はここらでは珍しい銀の髪をこてりと揺らす。そこに謙虚さはあれど意思なき愛想笑いはない。ただ純に楽しむための努力。グラナートとはそういう人物であった。
　彼女は生徒からも教師からも、どんな人からも好かれていた。聡明で美しく、穏健で、しかし強さを秘めている。惹かれるなという方が無理というもの。兄であるウェルドでさえ嫉妬を通り越して感心してしまうほどである。
　彼は、そんな完璧な妹とは違って、ごく普通の青年であった。`