











import {Vue, Component} from 'vue-property-decorator';
import Make1Titles, {EnumTitles} from '@/model/make1Titles.ts'
import TemplateMake1 from '@/components/template-make1-page.vue';
import TemplateComicDialog from '@/components/template-comic-dialog.vue'
import TemplateListOnly, {listItemType} from '@/components/template-list-only.vue'

@Component({
  components:{
    TemplateMake1,
    TemplateComicDialog,
    TemplateListOnly
  }
})
export default class CrystalConnection extends Vue{
  private intro = [ require('@/assets/make1/yt/01.png') ]
  private ep1 = [ require('@/assets/make1/yt/1_01.png'), require('@/assets/make1/yt/1_02.png'), require('@/assets/make1/yt/1_03.png'), require('@/assets/make1/yt/1_04.png'), require('@/assets/make1/yt/1_05.png'), require('@/assets/make1/yt/1_06.png'), require('@/assets/make1/yt/1_07.png'), require('@/assets/make1/yt/1_08.png'), require('@/assets/make1/yt/1_09.png'), require('@/assets/make1/yt/1_10.png'), require('@/assets/make1/yt/1_11.png'), require('@/assets/make1/yt/1_12.png'), require('@/assets/make1/yt/1_13.png'), require('@/assets/make1/yt/1_14.png'), require('@/assets/make1/yt/1_15.png'), require('@/assets/make1/yt/1_16.png'), require('@/assets/make1/yt/1_17.png'), require('@/assets/make1/yt/1_18.png') ]
  private ep2 = [ require('@/assets/make1/yt/2_01.png'), require('@/assets/make1/yt/2_02.png'), require('@/assets/make1/yt/2_03.png'), require('@/assets/make1/yt/2_04.png'), require('@/assets/make1/yt/2_05.png') ]

  private showDialog = false;
  private showStory = this.intro;
  private titles:listItemType[] = [
    {title:Make1Titles.contents[EnumTitles.YT].subTitles[0], icon:""},
    {title:Make1Titles.contents[EnumTitles.YT].subTitles[1], icon:require('@/assets/make1/yt/stage1-icon.png')},
    {title:Make1Titles.contents[EnumTitles.YT].subTitles[2], icon:require('@/assets/make1/yt/stage2-icon.png')},
  ]

  clickList(index:number){
    switch(index){
      case 0:
        this.showStory = this.intro;
        this.showDialog = true;
        break;
      case 1:
        this.showStory = this.ep1;
        this.showDialog = true;
        break;
      case 2:
        this.showStory = this.ep2;
        this.showDialog = true;
        break;
      default:
    }
  }
}
