




























import {Vue, Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class TemplateComicSDialog extends Vue{
  @Prop()
    pages!:any[];
  @Prop()
    show!:boolean;
  private isDevice = window.ontouchstart === null;
  private index = 0;
  private tapStartPos = 0;
  private first = true;

  updated(){
    if(this.first){
      if(this.isDevice){
        const ci = document.getElementById('comic-image');
        if(ci === null) return;
        ci.addEventListener('touchstart',(e)=>{
          this.tapStartPos = e.changedTouches[0].pageX;
        })
        ci.addEventListener('touchend', (e)=>{
          const tapEndPos = e.changedTouches[0].pageX;
          if(this.tapStartPos > tapEndPos){
            this.clickBack();
          }else{
            this.clickNext();
          }
        })
      }
      this.first = false;
    }
  }
  clickClose(){
    this.index=0;
    this.$emit('clickClose')
  }
  clickNext(){
    this.index = this.index < this.pages.length-1 ? ++this.index : this.index;
  }
  clickBack(){
    this.index = this.index > 0 ? --this.index : this.index;
  }
}
