









import {Vue, Component, Prop} from 'vue-property-decorator';
import Make1Titles, {EnumTitles}  from '@/model/make1Titles.ts';
import ListWithDialog, {itemType} from '@/components/template-list-with-dialog.vue'

@Component({
  components:{
    ListWithDialog
  }
})
export default class TemplateMeke1Page extends Vue{
  @Prop()
  titles!: itemType[];
//  @Prop()
//  titleImage: any = null;
  
  private isVertical = false;
  private imgForAnimation = true;

  // For reverse
  clickImg(){
    this.imgForAnimation = false;
    const pr = new Promise((res, rej)=>{
      setTimeout(()=>{
        res(this.imgForAnimation = true)
      }, 500)
    })
  }
}
