




















import { Component, Vue } from "vue-property-decorator";

import {EnumTitles} from '@/model/make1Titles.ts';
import SiteTitleBar from '@/components/site-title-bar.vue';
import MainNavigationBottom from '@/components/main-navigation-bottom.vue';
//import ListMake1 from './components/list-make1';
import AboutPage from '@/views/about-page.vue';
import Make1Page from '@/views/make1-page.vue';
import LinkPage from '@/views/link-page.vue';
import TopPage from '@/views/top-page.vue';

@Component({
  components: {
    SiteTitleBar,
    MainNavigationBottom,
    AboutPage,
    Make1Page,
    TopPage,
    LinkPage
  }
})
export default class App extends Vue {
  private isPC = false;
  private sideMenuDrwer = false;
  //private drwerList: any = null;
  private mainContent: any = TopPage;

  mounted(){
    if(navigator.userAgent.match(/(iphone|ipad|ipod|Android)/i)){
      this.isPC = false;
    }else{
      this.isPC = true;
    }
  }
  getAbout(){
    this.mainContent = AboutPage;
    //this.drwerList = null;
  }
  getTop() {
    this.mainContent = TopPage;
    //this.drwerList = null;
  }
  getMake1(){
    let beforeSame;
    if(this.mainContent == Make1Page){
      beforeSame = true;
    }
    this.mainContent = Make1Page;
    //this.drwerList = ListMake1;
    if(beforeSame)this.$refs.main.contentClick(EnumTitles.TOP);
  }
  getLink() {
    this.mainContent = LinkPage;
    //this.drwerList = null;
  }
}
