export const novel = `あるところにキョウダイがいました。
努力家で人懐っこい子と、誰とも関わろうとしないが何でも完璧にこなしてしまう子。それでも彼らの仲はよく、いつも一緒にいました。上の子は下の子がかわいかったし、下の子は上の子には絶対に逆らいません。
彼らは双子で、一部の色を除く姿形がそっくりでした。彼らにはもうひとつ下にキョウダイがいました。

「クノーテン。」
少年が、木陰で寝ている少年を呼び起こす。肩を掴んで軽く揺らすと、彼は目を覚ます。
「ん…どうした、セーレン？」
「暗くなった。帰らないとまた…」
日は傾き、木陰とそうでないところはやがて境界がなくなる。
「ばっかだなぁ、今日はもう帰らないんだよ。」
「どうして…？」
セーレンと呼ばれた少年は不思議そうにもう一人を見つめた。
「さっきも言っただろ？私は家出するの。」
「なぜ…？」
「とうさんが私のこと嫌いだから。なら、私から出てってやるよ。」
クノーテンと呼ばれていた少年は起き上がると、ひとつ伸びをする。
「ん～っ…と。」
その様を見てから、セーレンはもう一度疑問を示した。
「どうしてクノーテンが出ていくんだ？」
「…ま、必要なのはどう考えてもとうさんだろ？」
「そんなことない。あいつは『いらない』。」
クノーテンはため息をついて俯いたセーレンの両頬に手を当てる。
「そんなこと、言うもんじゃないぞ。とうさんはフィアデンの王で、私なんかよりもずっと強くて優しい。…じゃあな、セーレン。元気でやれよ。」
クノーテンはこつりとおでこを合わせて挨拶をすると、再び顔を上げ笑顔を見せた。
「…俺も行く。」
「お前は駄目だよ！…お前は、ここを離れちゃ…」
「クノーテンがいないなら、俺はここに居たくない。」
はあ、とクノーテンはため息をつくと、彼の性格を知っていたから、出かけた否定の言葉をのみ込み仕方ないなと頷いた。そうしてふたつは夕焼けの森を駆け抜けた。

「よもや、セルミアーデン様を連れ出すなんて。」
「あの子の重要性を知らないわけではないでしょうに。」
屋敷の掃除や世話をしているヒトビトが、扉を隔てて叱られている子供達に聞かれぬよう声を潜めて状況の確認をする。部屋の中ではクノーテンとセーレンが仲良く項垂れていた。
「それで、こんな時間にどこへ行こうとしていた。」
彼らの前でフィアデンがひとつ、机の書類に目を通したまま質問する。
「どこか…遠いところ。」
「なぜだ？」
「……」
黙ってしまったクノーテンを横目に見ると、セーレンは顔をあげる。
「あんたが悪い。あんたが、クノーテンをいじめるからだ。」
「おいっ！」
「いじめる…？」
不服そうな声音にクノーテンは慌ててセーレンの口を塞ぐと、汗を頬に浮かべながら苦笑いする。
「どういう意味だ？」
「わ、私が悪いんだよ。…私が、遠くまで行ってみたいなって…思って。」
「…なぜセーレンまで連れ出した。その子の役割を知らぬお前ではあるまい。」
クノーテンは一歩退いて口ごもる。彼の手が口から外れると、セーレンはきっと親を睨んだ。
「…お前なんか嫌いだ。」
「おちつきなさい、セーレン。悪いことをしたのはクノーテンだ。叱るのは親の務め。」
王はなだめるため声を丸めたが、彼はそんな説明に耳を貸そうとはしない。
「やめろよ…セーレン…」
呼ばれて少年は彼を振り向く。しかし何も言葉にせぬまま部屋を飛び出していった。取り残された親子の仲は、あまり良くない。
「…大方奴が行きたいと言ったのだろう。突き放せばよい。あれはお前の命令なら聞く。」
王は書類の端を机に落とし整える。
「そ、んなの…嫌だよ。あいつは人形じゃないんだから。きちんと自分の意思を持たなきゃ…」
冷たい言葉に彼はなるべく親の顔を見て弁明するが「それがしてはいけないことでもか。」と顔も上げずに言われて口をつぐんだ。
「…お前にも役割はある。いなくなってしまっては困る。」
クノーテンがお辞儀をし、部屋から出ようとすると、王はぽそりと、しかし聞こえるように呟いた。

「セーレン。」
少年は部屋に戻ったが、布団に寝転がっても目が冴えてしまい、どうしようもなくなって向かいの部屋をノックした。
「…クノーテン？入って。」
声と共に扉がまるで始めから映し出されていただけかのように姿を消す。クノーテンが足を動かそうと考えたとき、体は暗闇へと引きずられる。
「うわっ…と。」
少年は真っ暗な部屋の中、なにかにぶつかる。
「クノーテン…」
セーレンは布団にうずくまり、瞳から上だけを外にさらしている。クノーテンは首をかしげる。
「…眠れないのか？」
「ううん、そうじゃないけど…」
いててと足をさすり、ベッドへと座った。
「なあ、とうさんのことどうして嫌いなんだ？」
「クノーテンをいじめるからだ。」
「私…いじめられてなんかないけど。」
「じゃあなんでお前だけ部屋が違うんだ？」
セーレンの部屋は少年一人が住むには明らかに広い。それは唐草模様の金具装飾のついた大テーブルに数脚椅子があり、大人でも２、３つは裕に眠れるベッド、棚や引き出しが入っていて、である。比べてクノーテンの部屋は…扉から向かって左側は巨大な本棚に埋められ、残りのスペースに子供用のベッドと小さな机が置かれた、まるで倉庫に家具を詰め込んだようなところである。屋敷自体広く、部屋数に余裕はあった。それでも彼は、王に言われるがままずっとそこを使っていた。
「…使用人たちだって、もっと広い部屋を与えられてる。」
「あの人らは…仕事してるんだから。それに、結構狭いのもいいもんだし。」
「結果じゃない。始まりがおかしいんだ。」
「けど、私は…」
クノーテンは手を差し出す。そこから光が生まれると、やがて花の形を成し静かに散った。
「…魔法《フェル》もほら、手で支えてないと上手く使えないし…お前とはちが」
彼がすべてを言う前に、セーレンの金の瞳が歪んだ色を見せる。
「っ？！セーレン、おちつけ…」
「違う…違う…お前を…いらないなんて、イウナラ…」
「セーレン！！」
クノーテンは肩を叩きつかむ。彼ははっとすると片割れを穏やかに見つめた。
「…大丈夫か？」
「…」
彼は何も言わなかったが、なんとなく大丈夫でないことが伝わってきた。

クノーテンは他の子供達と花畑に集まり、一緒にクロルをやっていた。
「よしじゃあ配置につけー！」
一人が号令をかけると、バラバラと子供達は散らばる。そこにはヒトビトもフィアデンもコレンもいる。セーレンは窓からそんな彼らの笑顔を見つめていた。
「セルミアーデン様。」
目の細い長身の者が彼に話しかける。セーレンは振り向かず、窓の外を見続ける。
「スフォリア様がお呼びです。」
呼びに来た者はひとつお辞儀をする。少年は無視を続けた。
そんな態度の皇子にため息をつくと閉めてあった扉を少し開ける。そこからは少年の声がした。
「行こう。おとうさんが呼んでるよ。」
少年は手を胸の前に当てて上目遣いにきょうだいを見つめる。セーレンは頷きもせずに転移した。
「…お前だけか？」
「直に来るだろ。」
少年は親であるフィアデンを睨み付ける。彼はやれやれと言わんばかりの顔で椅子から立ち上がる。
「私がいつ、あの子をいじめていると？」
「昔からずっと。俺やアインを優先させている。」
「あの子はそれでもよいと言っている。」
「与えられないことに慣れているんだ…あんたのせいで。」
少年はきゅっと拳を握る。しかし光が点るよりはやく、扉が開いた。
「にーさん…」
「遅れました。申し訳ございません、スフォリア様…」
長身の者は深々と頭を下げる。
「ごくろうだった。準備を。」
「は。」
彼は出ていくと戸を閉める。
「喧嘩してたの？」
セーレンを呼びに来た少年―アインは恐る恐る家族のふたつを覗く。スフォリアは微笑むと彼を抱き上げた。
「いいや、少しお話をしていただけだよ。なあ、セーレン？」
「…まただ…」
スフォリアは続きを待つが、セーレンは呟くとしゃがみこんでしまう。
「？」と首をかしげたアインをスフォリアは下ろすと、彼に近づく。
「どうした？」
「……っ」
彼は頭を抱えたまましばらく震えていたが、やがて何でもないと立ち上がって首を振った。

「で、何してたんだ？」
「………」
セーレンはいつものようにクノーテンを部屋に招き入れると椅子へ座って創り出したお茶を飲んでいた。
「…別に。」
「なんだよー教えてくれたっていいじゃん。」
彼らは隣り合うように椅子を並べている。クノーテンはセーレンの肩を腕でつかんだ。
「それは…命令？」
「…ちげって、はあ…もういい。」
クノーテンは彼を解放するとふわりと浮いている茶の球をひとつつまんで飲み込む。セーレンはそんな彼を見つめて腕をつかんだ。
「っ…よくない、教えてほしいのか？お前が望むなら、俺は…」
突然体を揺らされ驚いてクノーテンはむせる。
「…お前さあ、嫌なら嫌って言えよ。…優しいんだから。気いつかってくれてるだろ？」
「ヤサシイ…？そうじゃない。言う必要がないだけだ。…それに、苦しくなる。」
セーレンは握っていた拳を開き自らの頭へと手を当てる。
「最近、よく苦しくなる…」
「大丈夫か？」
「今は…平気。」
クノーテンはぽんとたたくように頭を撫でる。
「無理すんなよ。」
セーレンは少し微笑んでこくりと頷いた。`