　「そしたらあいつが、スフォリアが、無理にでも抑え込めるようにって…クノーテンを人質にしたんだ。俺が上手くやらないなら、クノーテンを…ならず者の玩具にするって―」
　「え…」
　クノーテンはセーレンの頭から手を離すとやや後ずさる。その音には疑惑が戻っていた。
　あれから彼の元へそのようなモノ達が来なかったとこは1日たりともなかった。それは所詮その程度では、彼は自分を制御できなかったことを意味する
　「違うよっ俺、ちゃんと抑え込んでた！約束を破ったのはスフォリアなんだ…！俺は怒らないように解らなくならないように、…クノーテンのこと大切だもん。必死で抑えたのに……あいつは、お前の所へ奴らを送ることをやめなかった。解放しなかった。本当はただ、自分じゃどうしようもないあいつらを処理させたかっただけなんだ。生き物の欲望を、最も簡単に発散させるにはフィアデンを好きにさせるのが一番だから……」
　「嘘だ！父さんがそんなことするはずがない。」
　セーレンはまっすぐに弁解したが、クノーテンも負けずに返す。確かに自分との関係はよくなかっただろう。けれども悪いわけでもなかった。ただ父は、自分に興味がなかっただけだ。そうでなくとも王はいつだって正しい。そんな彼が、妥協的な解決で済ませたままにするだろうか…？
　「…だから助けに来たんだ。クノーテンが壊れちゃう前に。」
　セーレンはぐっと近づいていって再び抱きしめる。一瞬嫌悪的な戸惑いがあったが、かすかに震えているのが伝わってきて”彼も怖かった”のだと決めつけ頭を冷やして抱きしめ返した。
　「クノーテン…」
　セーレンの甘えるような声が耳に残る。わかっている。誰もこんなことを望んでなんかいない。
　「……っ！？」
　突然下腹部に刺激が走った。
　ようやく落ち着いたところでまた、状況がつかめなくなり、きちんとした”否定”もできずに”その”進入を許してしまう。
　「ひ…あ、が……ッ？」
　「クノーテン…ふふ、誰にも渡さない…」
　恐る恐る覗いたセーレンの瞳にまた、渦が巻いていた。欲が闇を飲み込むように、彼の真実が己の絶望と直感する。けれどもその刺激は、同化する恐怖はいつの間にか快感に変わってしまって、思うように力が入らない。
　―何が起こっている？
　もう何度同じ疑問を持っただろう。彼が恋慕に頬を染めて発した言葉が、図らずもその答えとなってクノーテンを正気に戻した。
　「あっは…クノーテンの中、きもちー…」
　「……やめろっ！！」
　中。
　そうだ、セーレンの一部が自分との融合を始めている。これはフィアデンの…
　生き物の交配に等しい行為だ。違いと言えば、どちらかが強く否定を示せばすぐにでも分離することと、繁栄には特に必要はないことというくらいだろう。
　弾かれるように思わず目を瞑ってセーレンはふらふらと2，3歩後ろへ退いた。
　「お前…ッ自分が何をしているのかわかってるのか！？」
　「なん…で……」
　「これじゃああのときと、あいつらと同じじゃないか！？せっかく消してくれたのに、どうしてこんな……」
　気持ち悪そうに口元を押さえているクノーテンを見ると、少年は反抗も怒りもなく静かに俯くだけだった。
　「そっか。クノーテンのは、そういうんじゃないんだ。」
　何のことかと尋ねても答えは得られない。彼はしんと静まりかえって地面を見つめていた。その顔に光はなかった。もう回りくどいのは面倒くさいと言わんばかりに笑顔を剥がして、クノーテンの手首は縄状の物で拘束される。
　「なっ…」
　「でも大丈夫。すぐにわかるから。」
　そのままぐいと万歳をさせられるみたいに腕を上げさせられ、どこかに固定された音がすると、それ以降大した身動きがとれなかった。
　「誰にも邪魔されないんだ。ゆっくり教えてあげるよ…」
　―恐怖だけが、私を支配した。
