export const novel = `　そこは、小さな小さな部屋だった。
　まるで本を置くことが目的のように、家具を詰め込んだような部屋だった。
　少年はベッドへ寝転がると無造作に敷かれた布団を抱き締める。もはやそこに温もりなど残ってはいないが、そうでもしないと寂しくてしんでしまいそうなのだ。
　あれから―クノーテンがクノーテンとして、フィアデンのひとつとして認められたあのときから、立場は逆転していた。セーレンは存在を消されて、クノーテンが皇子として仕事をしていた。クノーテンはよく働いた。もともと素質があった。セーレンは能力さえなければ、他から忌まれていたのだ。スフォリアの毛嫌いさえなければ…皆は初めからクノーテンを嫌うことなどなかったであろう。
　部屋も入れ換えられた。部屋には誰も来なかった。
　セーレンは迫り来る本棚の本にも、クノーテンの書き残した紙にも見向きもせず、ただじっとしていた。けれども彼は絶望も悲しみも感じてはいなかった。どこかで、彼が今準備をしているのだとわかっていた。ただ側に彼がいない寂しさだけはどうすることもできずに、こうして彼の眠っていた場所にいるのだった。
　
　「クノータデンス様。」
　「ん…？」
　振り向いた先でフィアデンがお辞儀をする。
　「少し、町へお願いできますか？」
　彼の表情から、それなりの困りごとがあるということは容易に想像できる。彼は頷くと、通りへ転移する。ざわざわ、ヒトビトやコレンが囁きあっていた。絶望したのか膝をつくものやフィアデンへ乞うようにしがみつくものもいた。
　「何があったんだ？」
　彼は声をかけてきたフィアデンへ問いかけながら辺りを見渡す。奥にスフォリアの姿もあった。
　「それが…復興が完全ではなかったようで。」
　クノーテンはセーレンの暴走を思い出す。
　「むしろ空間ごと抉られていたところは良かったのです。しかし、中途半端に失った土地の一部は残った部分を糧にしてしまったためか草木が全く…」
　確かに、彼の言うように土地には濁った色の土ばかりが散らばっている。
　「…なるほどね。」
　クノーテンは歩き出す。気づいたものたちが彼に道を譲り、件の土地の目の前まで来る。しゃがんで土を片手に掬った。
　「…うん、これなら大丈夫だな。」
　彼は立ち上がり手にした土をぱっと払う。こぼれ落ちたそれらから波紋が生まれて、どこまでも広がっていった。くるりと側のコレンを振り向く。
　「あんたでいいや。試しに種蒔いてみろよ。」
　言われてあたふたとしていたコレンであったが、皆の期待の視線を受けると土へ近づきちょんと触れる。側にいたフィアデンが栄養を与えれば、みるみる苗となり茎となる。わっと歓声が上がり、またきっと…フィアデンの株が上がった。クノーテンは戻ったことを確認するとまた別へと転移した。
　「…大人しく公務をこなしているようだな。」
　「いやあ、さすがですね。…どうかされました？」
　スフォリアはクノーテンの去り場を見つめたまま、目を細めていた。
　「いや、杞憂のようだ。」
　彼が言っているのは、クノーテンが冗談だと述べたことについてだ。そもそも"彼"とはそこまで深い関わりをしていなかったためか、冗談を言うところなどほとんど、あるいは全く見たことがなかった。加えて、あのような優越な表情も、自信で満ち溢れているところも、今までの彼からは想像がつかない。とはいえ、彼は忠実に―助言として意見をのべることはあれど―反抗もなくヒトビトやコレン、フィアデンを導いている。セーレンの難儀な性格を取り除いたような理想の個体であるとまで思えてくるのだ。計画も着々と進んでいる。彼は完全に力を制御できたし、必要以上をやらない。
　―だったら、何だというのだ。この、焦燥は…
　遅れて彼も"そこ"へ転移した。
　
　黒い岩壁に蒼の光が走るように強さを変えていた。フィアデンのうちで最も重要で神聖なる施設は限られたものだけが入れる固有空間にあった。
　広さはそれほどない。ひとつかふたつ用の演舞のステージのように少し上がった丸の台と、周りに柱を半分にしたものが囲っている程度である。
　「…ああ。…いやいや、……うん。」
　クノーテンは黒い衣に身を包み、台へ座り込むとひとつで相づちを打っていた。きらきらと岩を走る光が彼と戯れているようであった。
　そこへスフォリアが許可を申し出る。クノーテンは了承すると彼が顕現する。
　「…何か？」
　「ずいぶんと気さくなものだな。…会話もできるのか？」
　「そりゃ、私とセーレンができるんだから、簡単に。」
　王は以前セーレンとここへ来たことを思い出す。同期こそ容易だったものの、それ以上を彼はしなかった。できなかったのかやろうとしなかったのかは定かではないが。
　「で、本題は？そろそろ、私にこんなことさせる目的を話して欲しいな。」
　「…そうだな。」
　顎に手を当てるようにして考えるポーズを取るが、端からそのつもりはないのだろう、彼は話をそらす。
　「その衣は？セーレンのときと違うようだが。」
　「は？…ああ、なんかここ来ると勝手に変わるんだよね…あれの趣味じゃないか？」
　そんなものではないだろうと王がため息をつくと彼も返すように目を細める。
　「…まだ、な。お前が真にフィアデンのためを思っているのかが、どうにも。」
　「ちゃんと仕事はしてるだろ？私だって誰かのために力を使うのは嫌いじゃないし…まだ不満が？」
　言葉遣い？とクノーテンはからかったがスフォリアは動じずに例の場所へ来いとだけ述べる。
　「私に修行は必要ないぞ。」
　「傲るのはよくない。」
　「はいはい。」
　彼はひとつ伸びをするとぱっと姿を消した。
　
　クノーテンは"フィアデンにとって一番大切なこと"を終え、自室のベッドへ腰かける。仕事はたくさんあった。けれどもどれだけ魔法を使おうと気疲れ以外は疲労を感じない。
　―今まではちょこっと魔法を使うだけでもへばってたのにな… 
　じっと己の手を見つめる。自分の扱う力―しかしそれというよりも、もっと自分に近いように感じる。制御とか暴走とか…そういうのではないのだ。
　彼は「自分」を認識してようやく力を持ち、そしてセーレンが"なぜ暴走したのか"が理解できた。
　「…どうした？……なら、いいけど。」
　あの場所でなくとも、"それ"との会話は容易だった。こんこんとたたく音がして、気づいたクノーテンはそちらへ顔を向ける。
　「にーさん、いる？」
　「アインか。どうした？」
　扉が透けると幼子が不審そうにちらちらと反対を振り向いていた。
　「あのね、お向かいから変な音が聞こえるの。あそこ、ただの物置よね？」
　「向かい…？」
　クノーテンは彼の視線を追う。そこは自分の―セーレンがいるはずの部屋であった。
　少年は危ないかもしれないからとアインを部屋へ招き入れ待機するよう言い、扉を閉めると自分はそちらへ向かっていった。
　じっと、耳をすましてみる。
　確かに何か呻くような水滴のような不気味な音がした。
　クノーテンは一度だけ自室の戸と廊下を気にして、例の部屋へ転移する。
　「…クノーテンっ？！」
　当たり前だが、セーレンがいた。布団に横たわって…自らを抱き締めるように震えていた。顔の火照りから、それが恐怖や寒さによるものではないとすぐにわかる。
　「…何してんだ？」
　「わかんない…」
　ちょいちょいと彼は手招きするもクノーテンは首を振る。
　「お前は今いないことになってる。」
　「知ってる。邪魔する気もない。ただ…」
　彼はぺろりと自らの指を舐める。少年は冷ややかに見下した。
　「早くしろ。お前に付き合うほど暇じゃない。」
　「おねガい…きスだけ、して…おさえらレソうに、ない…」
　ああ、なるほどと彼は布団へ腰かけた。すぐにセーレンがしがみつく。
　「あいつが騒いでたのはお前のせいか。」
　「うん…ほんとは、めいワクかけたくなかったけど…」
　「いいよ。それでなんとかなるんなら。」
　セーレンとて、能力を失ったわけではない。同じ力をクノーテンが扱っているというだけの話で…
　少年は貪るように彼に食らいつく。荒い息づかいと、ぴちゃぴちゃという音が響いてクノーテンは嫌そうな顔をした。
　「……うん、もう大丈夫。」
　「あいつらさ、あんまわかってないよな。こんな風に閉じ込めたら余計暴走するだけだってのに…」
　「いいよ。…やりたいことあるんだろ？でもまだ探ってる？」
　クノーテンは扉の方へ向けていた視線を戻すともう一度彼に口づける。驚いたセーレンであったが、素直に舌を絡めた。
　「あ…珍しいな。お前からしてくれるなんて。」
　「必要だったらこれくらいなんともないさ。もう少しかかりそうだ。…ちょっと態度がまずかったかな。スフォリアがなかなか吐かない。」
　「けど、想像はついたんだろ？」
　「まあね。『魔法』の仕掛けはわかったさ。」
　セーレンは微笑んで頑張ってね、と呟く。
　「実験はそろそろ完了するし、そこから詰めてみるかなぁ…」
　クノーテンはぐっと伸びをして、アインが不安がったままだったと急いで帰っていく。彼の消えた光を見つめて、セーレンは再び転がった。
　アインは警戒するように扉を見つめていた。そこへクノーテンが戻ってくると恐怖と期待を向けてどうだったかときいた。
　「ん？ああ、大丈夫。もうなんともないよ。」
　「ほんと？」
　「けどな、コレンが入り込んじゃってて、大人にばれるとそいつが怒られちゃうから、内緒だぞ？」
　少年はしーっと指を口へ当てた。彼も目を輝かせて頷く。
　「あ、それとさ。」
　キョウダイが秘密、秘密と楽しむように呟いているのを眺め、先ほどのことを思い出した。
　「アイン、なんか魔法使ってみろ。」
　「え、どうして？」
　「いいからいいから。ほら、茶作ってみろよ。」
　アインは不思議そうな顔のまま手のひらを見つめる形で広げる。ぴくりと一瞬わずかに動いたものの、何も起こらなかった。
　「…あれ？」
　クノーテンはにこりと微笑む。
　「やっぱり、供給は食事か…」
　「どういうこと？」
　少年は視線を合わせると、ちゃんと言いつけ守ったんだな、と頭を撫でる。
　何が何やらわからなかったが、幼い彼は誉められただけで満足であった。`